import React from "react";
import PropTypes from "prop-types";
import {Link} from 'react-router-dom';
export default function Navbar(props) {
  return (
    <>
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <div className="container-fluid">
          <Link className="navbar-brand" to="/">
            {props.title}
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className="nav-link" aria-current="page" to={`/${props.first}`}>
                  {props.first}
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to={`/${props.second}`}>
                  {props.second}
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to={`/${props.third}`}>{props.third}</Link>
              </li>
            </ul>
            <button onClick={props.toggleTheme} className="btn-dark me-3 btn-sm btn">Toggle Dark Mode</button>
          </div>
        </div>
      </nav>
    </>
  );
}
Navbar.propTypes = {
  title: PropTypes.string.isRequired,
  first: PropTypes.string.isRequired,
  second: PropTypes.string.isRequired,
  third: PropTypes.string,
};
Navbar.defaultProps = {
  first: "Home",
};
